@import 'global';

$comments-btn-bgcolor: #292E34;

preview-control-bar {
  display: block;
}

.PreviewControlBar {
  width: 100%;
  height: 50px;
  color: $color-primary-text;
  background: $color-primary;

  nav > ul {
    margin: 0;
    display: flex;
    list-style: none;
    align-items: stretch;
    justify-content: space-between;
    padding-left: 0;
    height: 50px;

    & > li {
      margin-bottom: 0;
      line-height: 50px;
      display: flex;

      a, button, svg {
        height: 100%;
      }

      &.middleArea {
        button {
          background-color: transparent;
          background-image: none;
          border: 0;
          width: 48px;
          color: $color-primary-text;
          outline: none;
          margin-left: 0;

          &:active {
            outline: none;
          }

          .fa {
            font-size: 21px;
            &.fa-mobile {
              font-size: 24px;
            }
            &.fa-tablet {
              font-size: 23px;
            }
          }
        }
        input {
          background: transparent;
          border: none;
          padding: 5px 7px;
          width: 55px;
          align-self: center;
          line-height: 2em;
          text-align: center;
        }
      }

      &.rightArea {
        button, label[type='button'] {
          border: 0;
          padding: 3px 10px;
          background: $color-secondary;
          color: $color-secondary-text;

          &[disabled] {
            background: #292E34 !important;
            color: #4D5258 !important;
          }
        }
      }
    }
  }

  &__pageType, &__share {
    height: 26px !important;
    align-self: center;
    margin-right: 15px;
  }

  &__dashboard {
    padding: 0 15px;
    font-size: 14px;
    color: $color-primary-text;
    width: 190px;

    &:hover {
      text-decoration: none;
      color: $color-primary-text-hover;
    }

    .fa {
      margin-right: 10px;
    }
  }

  .PreviewControlBar__commentsBtn {
    padding: 0 15px;
    margin-left: 15px;
    height: 50px;
    border: 0;
    background-color: $color-primary-light;
    background-image: none;
    color: $color-secondary;
    border-right: 1px solid rgba(196, 196, 196, 0.4);
    border-left: 1px solid rgba(196, 196, 196, 0.4);

    &:hover, &.opened {
      background-color: $color-secondary;
      color: $color-primary;
    }
  }

  &__userDropdown {
    margin-right: 25px;

    .dropdown-toggle {
      color: $color-primary-text;
      
      &:hover {
        text-decoration: none;
      }
    }
  }

  .LanguageSelect {
    position: relative;
    border: solid $color-primary;
    border-width: 0 0 0 1px;
    margin-right: 25px;
  
    &__dropdown {
      padding: 0 24px 0 10px;
      border: 0;
      outline: none;
      background: $color-primary;
      color: $color-primary-text;
      font-size: 12px;
      cursor: pointer;
      appearance: none;
    }
  
    &__option {
      background: $color-secondary-text;
      color: $color-primary;
    }
  
    &__icon {
      position: absolute;
      top: 50%;
      right: 4px;
      transform: translateY(-52%);
      color: $color-primary-text;
      cursor: pointer;
      pointer-events: none;
    }
  }


}
