@import 'global';

preview-comments-bar {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: $color-primary-text;
  height: calc(100vh - 50px);
}

.PreviewCommentsBar {
  &__caption {
    display: flex;
    align-items: center;
    height: 52px;
    background: $color-primary-light;
    color: $color-primary-text;

    &-icon {
      color: $color-secondary;
      margin-left: 15px;
      &::before {
        width: 18px;
        height: 18px;
      }
    }

    h2 {
      flex: 1;
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      margin-left: 15px;
    }

    .PreviewCommentsBar__select-type {
      position: relative;
      border: solid $color-primary-light;
      border-width: 0 0 0 1px;
      margin-right: 0 15px;
      padding-top: 3px;
    
      &--dropdown {
        padding: 0 24px 0 10px;
        border: 0;
        outline: none;
        background: $color-primary-light;
        color: $color-primary-text;
        font-size: 12px;
        cursor: pointer;
        appearance: none;
      }
    
      &--option {
        background: $color-secondary-text;
        color: $color-primary;
      }
    
      &--icon {
        position: absolute;
        top: calc(50% + 2px);
        right: 8px;
        transform: translateY(-52%);
        color: $color-primary-text;
        cursor: pointer;
        pointer-events: none;
      }
    }

    .PreviewCommentsBar__close {
      background: $color-primary-light;
      border: 0;
      border-left: 1px solid rgba(196, 196, 196, 0.4);
      width: 52px;
      height: 52px;
    }
  }

  &__area {
    flex: 1;
  }
}